import React from 'react';
import { keyframes } from '@emotion/react';
import styled from '@emotion/styled';

import { useRealm } from '../../realm';

import { Login, UserDetail } from './realm';

import logo from './logo.svg';

const StyledSandbox = styled.div`
  text-align: center;
`;

const SandboxHeader = styled.header`
  background-color: ${({ theme }) => theme.colors.grays.base};
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: ${({ theme }) => theme.colors.white};
`;

const SandboxLink = styled.a`
  color: ${({ theme }) => theme.colors.blues.light1};
`;

const sandboxLogoSpin = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`;

const SandboxLogo = styled.img`
  height: 40vmin;
  pointer-events: none;
  @media (prefers-reduced-motion: no-preference) {
    animation: ${sandboxLogoSpin} infinite 20s linear;
  }
`;

function Sandbox() {
  const { user } = useRealm();
  return (
    <StyledSandbox>
      <SandboxHeader>
        <SandboxLogo src={logo} alt="logo" />
        <p>
          Edit <code>src/sandbox/sandbox.tsx</code> and save to reload.
        </p>
        <SandboxLink href="https://reactjs.org" target="_blank" rel="noopener noreferrer">
          Learn React
        </SandboxLink>
        {user ? <UserDetail user={user} /> : <Login />}
      </SandboxHeader>
    </StyledSandbox>
  );
}

export default Sandbox;
