import React, { useEffect, useState } from 'react';
import styled from '@emotion/styled';
import { Table, TableHeader, Row, Cell } from '@leafygreen-ui/table';
import { H2 } from '@leafygreen-ui/typography';

import { useAtlas } from '../../realm';
import { Season, Team, TeamStanding } from '../../types';

const Leagues = styled.div`
  display: flex;
`;

const Divisions = styled.div`
  display: flex;
  flex-direction: column;
`;

const Division = styled.div`
  display: flex;
  flex-direction: column;
`;

interface Props {
  season: Season;
}

interface MatchStats {
  wins: number;
  losses: number;
  gamesWon: number;
  gamesTotal: 0;
}

function Standings({ season }: Props) {
  const { data } = useAtlas();

  const [teamsByDivision, setTeamsByDivision] = useState<Record<string, TeamStanding[]>>({});
  useEffect(() => {
    if (!data || !season) {
      return;
    }
    Promise.all([
      data.teamMatches().find({ season_code: season.code }),
      data.teams().find({ 'season.code': season.code }),
    ]).then(([teamMatches, teams]) => {
      const teamResults = teamMatches.reduce<Record<number, MatchStats>>((acc, teamMatch) => {
        const leftTeam = acc[teamMatch.left_team.code] || { wins: 0, losses: 0, gamesWon: 0, gamesTotal: 0 };
        teamMatch.left_team.win ? leftTeam.wins++ : leftTeam.losses++;
        leftTeam.gamesWon += teamMatch.left_team.games_won;
        leftTeam.gamesTotal += teamMatch.left_team.games_won + teamMatch.right_team.games_won;

        const rightTeam = acc[teamMatch.right_team.code] || { wins: 0, losses: 0, gamesWon: 0, gamesTotal: 0 };
        teamMatch.right_team.win ? rightTeam.wins++ : rightTeam.losses++;
        rightTeam.gamesWon += teamMatch.right_team.games_won;
        rightTeam.gamesTotal += teamMatch.right_team.games_won + teamMatch.left_team.games_won;

        return {
          ...acc,
          [teamMatch.left_team.code]: leftTeam,
          [teamMatch.right_team.code]: rightTeam,
        };
      }, {});

      const teamsByDivision = teams.reduce<Record<string, Team[]>>(
        (acc, team) => ({
          ...acc,
          [team.division_name]: [...(acc[team.division_name] || []), team],
        }),
        {},
      );

      setTeamsByDivision(
        Object.entries(teamsByDivision).reduce((acc, [division, teams]) => {
          const standings = teams.map(team => {
            const results = teamResults[team.key];
            return {
              ...team,
              rank: 0,
              wins: results.wins,
              losses: results.losses,
              pct: results.gamesWon / results.gamesTotal,
            };
          });

          standings.sort((s1, s2) =>
            s1.wins === s2.wins
              ? s1.losses === s2.losses
                ? s2.pct - s1.pct
                : s1.losses - s2.losses
              : s2.wins - s1.wins,
          );

          return {
            ...acc,
            [division]: standings.map((s, i) => ({ ...s, rank: i + 1 })),
          };
        }, {}),
      );
    });
  }, [season]);

  if (!data) {
    return null;
  }

  return (
    <Leagues>
      {season.leagues.map(league => (
        <Divisions key={league}>
          {season.divisions[league].map(division => (
            <Division key={division}>
              {teamsByDivision[division] && (
                <>
                  <H2>{division}</H2>
                  <Table
                    data={teamsByDivision[division]}
                    columns={[
                      <TableHeader label="Place" />,
                      <TableHeader label="Team Name" />,
                      <TableHeader label="W's" />,
                      <TableHeader label="L's" />,
                      <TableHeader label="PCT." />,
                    ]}
                  >
                    {({ datum: ts }) => (
                      <Row key={ts.key}>
                        <Cell>{ts.rank}</Cell>
                        <Cell>{ts.name}</Cell>
                        <Cell>{ts.wins}</Cell>
                        <Cell>{ts.losses}</Cell>
                        <Cell>{ts.pct.toFixed(3)}</Cell>
                      </Row>
                    )}
                  </Table>
                </>
              )}
            </Division>
          ))}
        </Divisions>
      ))}
    </Leagues>
  );
}

export default Standings;
