import React from 'react';
import { keyframes } from '@emotion/react';
import styled from '@emotion/styled';
import Button from '@leafygreen-ui/button';
import { MongoDBLogoMark } from '@leafygreen-ui/logo';
import { Body, H1 } from '@leafygreen-ui/typography';

import { useRealm } from '../realm';

const Header = styled.header`
  text-align: center;
`;

const Title = styled.div`
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-column-gap: 5px;
  justify-items: center;
`;

const UserInfo = styled.div`
  margin-left: auto;
  text-align: right;
  padding-right: 0.5rem;
`;

const leafAnimation = keyframes`
  from {
    transform: rotate(-5deg);
  }
  to {
    transform: rotate(5deg);
  }
`;

const StyledLogo = styled(MongoDBLogoMark)`
  display: inline-block;
  margin: 24px;
  animation: ${leafAnimation} 3s infinite alternate-reverse ease-in-out;
  transform-origin: 50% 100%;
`;

function App() {
  const { logout, user } = useRealm();
  return (
    <Header>
      <Title>
        <H1 style={{ gridColumnStart: 2 }}>Amsterdam Billiards League Site</H1>
        {user && (
          <UserInfo>
            <Body>{`Logged in as ${user.providerType === 'anon-user' ? 'guest' : user.profile.email || user.id}`}</Body>
            <Button variant="primaryOutline" onClick={logout}>
              Logout
            </Button>
          </UserInfo>
        )}
      </Title>
      <StyledLogo height={98} />
    </Header>
  );
}

export default App;
