import React, { useEffect, useMemo, useState } from 'react';
import styled from '@emotion/styled';
// import Button from '@leafygreen-ui/button';
import { Option, Select } from '@leafygreen-ui/select';
import { InlineCode } from '@leafygreen-ui/typography';

import { useAtlas } from '../../realm';
import { PlayerMatch, Season, ScheduledWeek, Team, TeamMatch } from '../../types';

const StyledMain = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  justify-content: center;
`;

const StyledSelects = styled.div`
  display: flex;
`;

interface Props {
  seasons: Season[];
  teams: Team[];
}

type WeeklyResult = ScheduledWeek &
  Partial<Pick<TeamMatch, 'fifth_set' | 'left_team' | 'right_team'>> & {
    matches: Partial<Pick<PlayerMatch, 'type' | 'is_forfeit' | 'left_player' | 'right_player'>>[];
  };

function TeamPage({ seasons, teams }: Props) {
  const { data } = useAtlas();

  const [selectedSeason, setSelectedSeason] = useState<Season>();
  const [selectedTeam, setSelectedTeam] = useState<Team>();

  const team = useMemo(() => teams.find(t => t.name === selectedTeam?.name), [selectedTeam, teams]);

  const [schedule, setSchedule] = useState<WeeklyResult[]>([]);
  useEffect(() => {
    if (!data) {
      return;
    }

    Promise.all([
      data.schedule().find({ 'season.code': selectedSeason?.code, 'season.day': team?.season.day }),
      data.teamMatches().find({
        season_code: selectedSeason?.code,
        $or: [{ 'left_team.code': selectedTeam?.key }, { 'right_team.code': selectedTeam?.key }],
      }),
      data.playerMatches().find({
        season_code: selectedSeason?.code,
        $or: [{ 'left_player.team_code': selectedTeam?.key }, { 'right_player.team_code': selectedTeam?.key }],
      }),
    ]).then(([weeks, teamMatches, playerMatches]) => {
      const teamMatchesByWeek = teamMatches.reduce<Record<number, TeamMatch>>(
        (acc, teamMatch) => ({ ...acc, [teamMatch.week_num]: teamMatch }),
        {},
      );

      const playerMatchesByWeek = playerMatches.reduce<Record<number, PlayerMatch[]>>(
        (acc, playerMatch) => ({ ...acc, [playerMatch.week_num]: [...(acc[playerMatch.week_num] || []), playerMatch] }),
        {},
      );

      setSchedule(
        weeks
          .map(week => {
            const teamMatch = teamMatchesByWeek[week.week_num];
            if (!teamMatch) {
              return { ...week, matches: [] };
            }

            const { fifth_set, left_team, right_team } = teamMatch;
            const playerMatches = playerMatchesByWeek[week.week_num];
            return {
              ...week,
              fifth_set,
              left_team,
              right_team,
              matches: playerMatches.map(({ type, is_forfeit, left_player, right_player }) => ({
                type,
                is_forfeit,
                left_player,
                right_player,
              })),
            };
          })
          .sort((w1, w2) => w1.week_num - w2.week_num),
      );
    });
  }, [data, selectedSeason?.code, selectedTeam?.key, team?.season.day]);

  if (!data) {
    return null;
  }

  return (
    <StyledMain>
      <StyledSelects>
        <Select
          style={{ width: '360px' }}
          label="Choose a season:"
          placeholder="Select..."
          onChange={val => setSelectedSeason(seasons.find(s => s.name === val))}
        >
          {seasons.map(({ code, name }) => (
            <Option key={code} value={name}>
              {name}
            </Option>
          ))}
        </Select>
        <Select
          style={{ width: '360px' }}
          label="Choose a team:"
          placeholder="Select..."
          disabled={!selectedSeason}
          onChange={val => setSelectedTeam(teams.find(t => t.name === val))}
        >
          {teams
            .filter(team => team.season.name === selectedSeason?.name)
            .map(({ key, name }) => (
              <Option key={key} value={name}>
                {name}
              </Option>
            ))}
        </Select>
      </StyledSelects>
      {selectedTeam && (
        <>
          {schedule.map(s => (
            <pre style={{ marginTop: '1rem', padding: '0.5rem', border: '1px solid' }}>
              <InlineCode>{JSON.stringify(s, null, 2)}</InlineCode>
            </pre>
          ))}
        </>
      )}
    </StyledMain>
  );
}

export default TeamPage;
